<template>
  <div class="wrapper" >
    <!-- 轮播图开始 -->
    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
      <van-swipe-item v-for="(item,index) in banner" :key="index">
        <img :src="item.banner_img" alt />
      </van-swipe-item>
    </van-swipe>
    <!-- 轮播图结束 -->
    <!-- 导航开始 -->
    <div class="menu">
      <ul>
        <li @click="tsk">
          <img
            src="data:image/png;base64,"
            alt
          />

          <p>特色课</p>
        </li>
        <li>
          <img
            src="data:image/png;base64,"
            alt
          />

          <p>一对一辅导</p>
        </li>
        <li>
          <img
            src="data:image/png;base64,"
            alt
          />
          <p>工作日历</p>
        </li>
      </ul>
    </div>
    <!-- 导航结束 -->
    <!-- 首页列表开始 -->
    <div v-for="(item,index) in List" :key="index">
      <div class="nav" v-if="item.channel_info.type == 3">
        <ul>
          <h3>{{ item.channel_info.name }}</h3>
          <br />
          <!-- <van-cell> -->
          <li v-for="(v,i) in item.list" :key="i" @click="showPopup(v.teacher_id)">
            <div class="left">
              <img :src="v.teacher_avatar" alt />
            </div>
            <div class="right">
              <p class="nav-con">{{ v.teacher_name }}</p>
              <p class="content">{{ v.introduction }}</p>
            </div>
          </li>
          <!-- </van-cell> -->
        </ul>
      </div>

      <div class="cons" v-if="item.channel_info.type == 1">
        <ul>
          <h3>{{ item.channel_info.name }}</h3>
          <br />
          <li v-for="(j,k) in item.list" :key="k" v-detail="path" @click='statDetail(j.id,j.course_type)'>
            <p>{{ j.title }}</p>
            <br />
            <span>共{{j.total_periods }}课时</span>
             <img src="../assets/微信图片_20200716180913.png"   alt="" v-if="j.has_buy==1" class="image">
            <br />
            <p v-for="(s,y) in j.teachers_list" :key="y">
              <img :src="s.teacher_avatar" alt />
              {{ s.teacher_name }}
            </p>
            <br />
            <hr />
            <br />
            <div class="bz">
              <span>已有{{ j.sales_num }}报名</span>
              <span class="mat">{{ j.price | priceShow }}</span>
            </div>
          </li>
        </ul>
      </div>
      <div class="nav" v-if="item.channel_info.type == 4">
        <ul>
          <h3>{{ item.channel_info.name }}</h3>

          <br />
         
            <li v-for="(a,b) in item.list" :key="b"  @click="showPopup">
              <div class="left">
                <img :src="a.teacher_avatar" alt />
              </div>
              <div class="right">
                <p>
                  {{ a.teacher_name }}
                  <span
                    style="color:red;font-size:16px;font-weight:bold;"
                  >{{ a.level_name }}</span>
                </p>
                <p class="content">{{ a.introduction }}</p>
              </div>
            </li>
         
        </ul>
        <!-- <xf></xf> -->
      </div>
    </div>
    <!-- 首页列表结束 -->
    <!-- //点击名师阵容出现的面板 -->
    <van-popup >
      <div class="tan">
        <img src="../assets/1594515660(1).png" alt />
        <h4>赶紧登陆一下吧</h4>
        <p style="margin-top:15px;margin-bottom:15px;">预约一对一辅导，浏览更多视频课程~~</p>
        <button class="btn">
          <router-link  style="color:#fff;">立即登录</router-link>
        </button>
      </div>
    </van-popup>
    <!-- 悬浮面板结束 -->
  </div>
</template>

<script>
import axios from'axios'
export default {
  components: {
    
  },
  props: {},
  data() {
    return {
      path: "/detail",
      id: "",
      banner: [],
      List: [],
      arr:'',
      arrlist:''
      // isShow:false
    };
  },
  filters: {
    priceShow(val) {
      if (val == 0) {
        return "免费";
      } else {
        return `￥${(val / 100).toFixed(2)}`;
      }
    }
  },
  watch: {},
  computed: {},
  methods: {
    tsk(){
     this.$router.push("/home/kc")
    },
    showPopup(v) {
       let toke= localStorage.getItem("adminToken")
        if(toke){
          this.$router.push({
             path:"/teacher",
             query:{
                 id:v,
             }

          })
        }else{
           this.$router.push("/home/wd")
             
        }
    },
    //轮播图接口获取
    async AjaxSwiper() {
      let { data: res } = await this.$http.get(
        "/api/app/banner"
      );
      console.log(res, 123);
      this.banner = res.data;
    },
    //首页数据列表获取
    async AjaxList() {
      let { data: res } = await this.$http.get(
        "/api/app/recommend/appIndex"
      );
      //  if(res.data)
      
      this.List = res.data;
      
      
    },
    //课程详情页
     statDetail(id,type){
           
          this.$router.push({
            path:'/detail',
            query:{
              id:id,
              type:type
            }
          })
     }






  },
  created() {
   
    
     
  },
  mounted() {
     
    this.AjaxSwiper();
    this.AjaxList();
   
  }
};
</script>
